.app__main_futurePlan{
    background-color: var(--color-black);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding:2rem 0;
    width:100%;
}

.app__main_futurePlan-titleBox{
    width:80%;
    display: flex;
    flex-direction: column;

    margin: 3rem 0;
}

.app__main_futurePlan-title{
    color:white;
    font-size: 32px;
}

.app__main_futurePlan-titleBar{
    width:12rem;
    height:1px;
    background: var(--color-golden);
}

/*내용*/
.app__main_futurePlan-content{
    width:80%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.app__main_futurePlan-contentBox{
    width:100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.app__main_futurePlan-content-title{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;

    margin-bottom: 20px;
}

.app__main_futurePlan-content-title > .content-title{
    padding:0.8rem 2rem;
    border-radius: 25px;
    font-size: 25px;
    color:white;
}

.app__main_futurePlan-content-lists{
    display:flex;
    flex-direction: column;
}

.app__main_futurePlan-content-lists > .content-lists{
    display:flex;
    flex-direction: row;
    justify-content: flex-start;
    background-color: white;
    border-radius: 10px;
    padding:0.5rem 1.8rem;
    margin-bottom: 10px;

    .content-list{
        margin-left:10px;
        font-weight: bold;
    }
}


@media screen and (max-width:768px) {
    .app__main_futurePlan-titleBox{
        width:70%;
    }
    
    .app__main_futurePlan-title{
        font-size: 28px;
    }
    
    .app__main_futurePlan-titleBar{
        width:10rem;
    }

    .app__main_futurePlan-content{
        width:70%;
    }
    
    .app__main_futurePlan-contentBox{
        margin:0 30px;
    }
    
    .app__main_futurePlan-content-title > .content-title{
        padding:0.5rem 1.5rem;
        border-radius: 20px;
        font-size: 20px;
    }

    .app__main_futurePlan-content-lists > .content-lists{
        padding:0.3rem 1.3rem;
        .content-list{
            font-size:15px;
        }
    }
}

@media screen and (max-width:650px){
    .app__main_futurePlan-titleBox{
        width:60%;
    }
    
    .app__main_futurePlan-title{
        font-size: 25px;
    }

    .app__main_futurePlan-content{
        flex-direction: column;
    }

    .app__main_futurePlan-contentBox{
        margin:30px;
    }

    .app__main_futurePlan-content-title > .content-title{
        padding:0.8rem 2rem;
        border-radius: 25px;
        font-size: 25px;
    }

    .app__main_futurePlan-content-lists > .content-lists{
        padding:0.5rem 1.5rem;
        .content-list{
            font-size:16px;
        }
    }
}