/**모달 */
.app__projects-pop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    
    display: flex;
    align-items: center;
    justify-content: center;
    z-index:6;
}

.modal {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 70vh;
    width: 50%; 
    margin: auto; 
    overflow-y: scroll;
    background: rgb(255, 248, 223);
    padding:1rem 3rem; 
    border-radius: 10px;
}

/* 스크롤바의 폭 너비 */
.modal::-webkit-scrollbar {
    width: 10px;  
}

.modal::-webkit-scrollbar-thumb {
    background: rgb(220, 20, 103); /* 스크롤바 색상 */
    border-radius: 10px; /* 스크롤바 둥근 테두리 */
}

.modal::-webkit-scrollbar-track {
    background: rgba(220, 20, 103, .1);  /*스크롤바 뒷 배경 색상*/
}

/*모달 내용*/
.modal-content{
    position:absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:flex-end;

    margin:10px;
}

.modal-content .modal-content-text{
    margin-top:2rem;
    color:black;
}

.modal-content .modal-content-text > *{
    margin:1rem;
}

/*사이트 대략 설명*/
.content-name{font-size: 48px;}
.content-description{font-size: 18px;}
.content-skill{display: inline;background: linear-gradient(to top, #ffdaeb 40%, transparent 40%);}

/*사이트 구체적 설명*/
.content-pages .content-page .content-page-title{
    color:rgb(80, 80, 80);
    font-size: 23px;
    display: inline;
    background: linear-gradient(to top, #feffb4 50%, transparent 40%);
}

.content-pages .content-page .content-page-description{
    margin:10px 0;
}
/*사이트 이미지들*/
.modal-content .content-imgs{
    position:relative;
    min-width: 100px;
    height:300px; 
}

.modal-content .content-imgs img{
    height:100%;
    margin:10px;
    object-fit: contain;
}

@media screen and (max-width:768px){
    .content-name{font-size: 36px;}
    .content-description{font-size: 16px;}
    .content-pages .content-page .content-page-title{
        font-size: 18px;
    }
}

@media screen and (max-width:650px){
    .modal-content{
        top:0;
        left:0;
    }
    .modal-content .modal-content-text{
        margin-top:1rem;
    }
    .content-name{font-size: 32px;}
    .content-description{font-size: 15px;}
    .content-pages .content-page .content-page-title{
        font-size: 16px;
    }
}