
.app__main_aboutme{
    background-color: var(--color-black);
    display: flex;
    flex-direction: row;
    justify-content:center;
    align-items: center;

    padding:3rem 0;
    width:100%;
}

.app__main_aboutme > * {
    margin:0 3rem;
}

.app__main_aboutme-img{
    padding:1rem;
}

.app__main_aboutme-content{
    display: flex;
    flex-direction: column;

    padding:1rem;
}

.app__main_aboutme-content > * {
    margin-top:10px;
}

.app__main_aboutme-title{
    color:white;
}

.app__main_aboutme-title:first-child{
    font-size: 28px;
    font-weight: 600;
}

.app__main_aboutme-sub{
    width:100%;
    height:100%;
}

.app__main_aboutme-box{
    width:100%;
    height:100%;
    padding:1rem;
    background-color: white;
    border-radius: 10px;
}

.app__main_aboutme-boxContent{
    color:var(--color-black);
    font-weight: 600;

    margin-top:3px;
}

@media screen and (max-width:2000px){

}

@media screen and (max-width:1150px) {

}

@media screen and (max-width:768px) {
    .app__main_aboutme > * {
        margin:0 15px;
    }
    .app__main_aboutme-img{
        margin-left:50px;
    }

}

@media screen and (max-width:650px){
    .app__main_aboutme{
        flex-direction: column;
        width:100%;
    }
}
